import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import './App.scss';
import MainPage from './components/MainPage/MainPage';
import TaskPage from './components/TaskPage/TaskPage';
import Nav from './components/Layers/Nav/Nav';
import Footer from './components/Layers/Footer/Footer';
import ProfilePage from './components/ProfilePage/ProfilePage';
import BlogPage from './components/BlogPage/BlogPage';
import ArticlePage from './components/ArticlePage/ArticlePage';
import RefPage from './components/RefPage/refPage';
 
const basename = process.env.NODE_ENV === 'production' ? '/PixiReact/' : '/';
console.log(basename)
function App() {
  return (
    <Router  basename={basename}>
    <Nav/>
      <Routes>
        <Route path="/" element={<MainPage />} />
        <Route path="/profile" element={<ProfilePage />} />
        <Route path="/tasks" element={<TaskPage />} />
        <Route path="/blog" element={<BlogPage />} />
        <Route path="/article" element={<ArticlePage />} />
        <Route path="/ref" element={<RefPage />} />
      </Routes>
    <Footer/>
    </Router>
  );
}

export default App;
